import React, { Component } from 'react';

class Footer extends Component {

    constructor(props){
        super(props)
        this.state = {
            year: new Date().getFullYear()
        }
    }

  render() {

    if(this.props.data){
      var networks= this.props.data.social.map(function(network){
        return <li key={network.name}><a title={network.name} target="_blank" rel="nofollow noopener noreferrer" href={network.url}><i className={network.className}></i></a></li>
      })
    }

    return (
      <footer>

     <div className="row">
        <div className="twelve columns">
           <ul className="social-links">
              {networks}
           </ul>

           <ul className="copyright">
              <li>&copy; Copyright {this.state.year} <a title="Danilo Sambugaro" rel="author" href="https://dsambugaro.github.io/">Danilo Sambugaro</a></li>
              <li>Design by <a title="Styleshout" target="_blank" rel="nofollow noopener noreferrer" href="http://www.styleshout.com/">Styleshout</a></li>
           </ul>

        </div>
        <div id="go-top"><a className="smoothscroll" title="Back to Top" href="#home"><i className="icon-up-open"></i></a></div>
     </div>
  </footer>
    );
  }
}

export default Footer;
